import Vue from 'vue'
import Vuex from 'vuex'
import router from '../router'
import productList from '../product_list'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cartList: [],
  },
  mutations: {
    addToCart(state, payload) {
      for (let cart of state.cartList) {
        if (cart.id === payload.id) {
          cart.qty += 1
          return;
        }
      }

      state.cartList.push({
        ...payload,
        qty: 1
      })
    },

    removeFromCart(state, payload) {
      const index = state.cartList.indexOf(payload)
      state.cartList.splice(index, 1)
    },

    increaseItem(state, payload) {
      const index = state.cartList.indexOf(payload)
      const item = state.cartList[index]
      item.qty += 1
      Vue.set(state.cartList, index, item)
    },

    decreaseItem(state, payload) {
      const index = state.cartList.indexOf(payload)
      const item = state.cartList[index]

      if (item.qty > 1) {
        item.qty -= 1
        Vue.set(state.cartList, index, item)
      }
    },
    clearCartList(state) {
      state.cartList = []
    }
  },
  actions: {
    addToCart({ state, commit }, payload) {
      commit('addToCart', payload)
    },
    removeFromCart({ state, commit }, payload) {
      commit('removeFromCart', payload)
    },
    increaseItem({ state, commit }, payload) {
      commit('increaseItem', payload)
    },
    decreaseItem({ state, commit }, payload) {
      commit('decreaseItem', payload)
    },
    clearCartList({ state, commit }, payload) {
      commit('clearCartList')
    },
    updateProductList({getters}, type) {
      router.push({ query: { category: type } })
        .catch(err => {})
    }
  },
  getters: {
    productList: (state) => {
      return productList
      // type = type || router.currentRoute.query.category
      //
      // if (!type) return productList
      //
      // return productList.filter(item => {
      //   return item.category === type
      // })
    },

    totalAmount: state => {
      return state.cartList
        .map(item => item.qty * item.price)
        .reduce((prev, current) => prev + current, 0)
    }
  },
  modules: {}
})
